<template>
  <img alt="Vue logo" src="./assets/logo.png" width="50" />
  <div class="container">
    <div class="chatbox">
      <div class="chatmsg" v-for="(item, i) in msg_list" :key="i">
        <div class="chatinfo">
          <span class="ctime">{{ item.ctime }}</span>
          <span>
            <span class="mid-bracket" :style="{ color: item.user.color }">{{
              item.user.name
            }}</span>
            <span class="sml-bracket">{{ item.user.uid }}</span>
          </span>
        </div>

        <span>{{ item.text }}</span>
      </div>
    </div>
    <pre></pre>
    <n-color-picker v-model:value="user.color" />
    <div class="chatinput">
      <span class="uid"> {{ user.uid ? user.uid : "无服务" }} </span>
      <input
        class="uname"
        type="text"
        v-model="user.name"
        :style="{ color: user.color }"
      />
      <input class="uinput" type="text" v-model="text" />
      <button @click="send">send</button>
    </div>
  </div>
</template>

<script setup>
import { onMounted, onUpdated, reactive, ref } from "vue";

Number.prototype.format = function (len) {
  let prefix = new Array(len).fill("0").join("");
  let result = prefix + String(this);
  result = result.slice(-len);
  return result;
};

Date.prototype.get_time_s = function () {
  let y = this.getFullYear();
  let M = (this.getMonth() + 1).format(2);
  let d = this.getDate().format(2);
  let h = this.getHours().format(2);
  let m = this.getMinutes().format(2);
  let s = this.getSeconds().format(2);

  let date = `${y}/${M}/${d}`;
  let time = `${h}:${m}:${s}`;
  return date + " " + time;
};

const msg_list = reactive([]);
const text = ref("测试123");
const user = reactive({
  name: "匿名",
  uid: "",
  color: "#000",
});

onMounted(() => {
  let old_user = JSON.parse(localStorage.getItem("user"));
  if (old_user) {
    for (let key in old_user) user[key] = old_user[key];
  }
});

onUpdated(() => {
  let chatbox = document.querySelector(".chatbox");
  chatbox.scrollTop = chatbox.scrollHeight;
  localStorage.setItem("user", JSON.stringify(user));
});

//------------------------------初始化weosocket-------------------------
import WS from "./ws.js";

// websocket地址
let uri =
  process.env.NODE_ENV == "development"
    ? "ws://localhost:5000/ws"
    : window.location.href.replace("http", "ws") + "ws";
var ws = WS(uri);

ws.on("connect", () => {
  ws.send({
    type: "login",
    uid: user.uid,
    name: user.name,
    color: user.color,
  });
});

ws.on("message", (data) => {
  msg_list.push(data);
});

ws.on("init", (data) => {
  user.uid = data.uid;
  data.history.forEach((element) => {
    msg_list.push(element);
  });
});

//------------------------------用户发送消息-------------------------

const send = () => {
  let data = {
    type: "message",
    user: { ...user },
    text: text.value,
    ctime: new Date().get_time_s(),
  };
  msg_list.push(data);
  ws.send(data);
};
</script>

<style>
#app {
  font-family: Consolas, Helvetica, Arial, sans-serif;
  color: #2c3e50;
}

.container {
  position: relative;
  left: 50%;
  transform: translateX(-50%);
  width: 400px;
}

.chatbox {
  display: flex;
  flex-direction: column;
  align-items: stretch;
  justify-content: flex-start;
  height: 400px;
  border: 1px solid black;
  overflow: auto;
}

.chatinput {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.msg {
  line-height: 20px;
  width: 100%;
}

.uid {
  width: 70px;
}

.uname {
  width: 80px;
}

.uinput {
  width: 220px;
}

.chatmsg:nth-child(odd) {
  background-color: #eee;
}

.chatmsg {
  padding: 4px;
}

.mid-bracket::before {
  content: "[";
  color: black;
}

.mid-bracket::after {
  content: "]";
  color: black;
}

.sml-bracket::before {
  content: "(";
  color: black;
}

.sml-bracket::after {
  content: ")";
  color: black;
}

.ctime {
  color: green;
}

.chatinfo {
  font-size: 12px;
  display: flex;
  justify-content: space-between;
}
</style>
