function WS(uri) {
    var websock, timer, wait = 1;
    var dealers = { connect: () => { } };
    var ws = {};

    ws.init = () => {
        console.log("初始化websocket...");
        websock = new WebSocket(uri);
        websock.onopen = onopen;
        websock.onmessage = onmessage;
        websock.onerror = onerror;
        websock.onclose = onclose;
    };

    ws.on = (type, func) => {
        dealers[type] = func;
    };

    ws.send = (data) => {
        console.log("send", data);
        websock.send(JSON.stringify(data));
    };

    var onopen = () => {
        //连接成功
        wait = 1;
        console.log("连接成功");
        // 添加心跳检测，每30秒发一次数据，防止连接断开
        //（跟服务器设置有关，如果服务器没有设置每隔多长时间不发消息断开，可以不进行心跳设置）
        timer = setInterval(() => {
            if (websock.readyState == 1) ws.send({ type: "heartbeat" });
        }, 30000);

        dealers.connect();
    };

    var onmessage = (e) => {
        //接收后端返回的数据
        let data = JSON.parse(e.data);
        console.log("recv", data);

        let type = data.type;
        if (type in dealers) dealers[type](data);
    };

    var reconnect = () => {
        // 避让算法
        wait = wait * 2;
        setTimeout(() => {
            ws.init();
        }, wait * 1000);
    };


    var onerror = (e) => {
        //连接失败
        console.log("连接失败", e);
    };

    var onclose = (e) => {
        //关闭连接
        console.log("断开连接", e);
        clearInterval(timer);

        // [待修改] 目前自动重连，等开发后期要改
        reconnect();
    };

    ws.init();

    return ws;
}


export default WS;



